import Vue from 'vue';
import Vuex from 'vuex';
import data from "../constant";

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        news: data.rows,
        spinning: false,
        language: 'en',
        user: null,
    },
    mutations: {
        createNews (state, item) {
            state.news.unshift(item);
        },
        deleteNews (state, id) {
            state.news.splice(id, 1);
        },
        setSpinning (state, value) {
            state.spinning = value;
        },
        setLanguage (state, lang) {
            state.language = lang;
        },
        saveUser (state, username) {
            state.user = username;
        },
        logOut (state) {
            state.user = null;
        }
    },
    getters: {
        indexLastItem: (state) => {
            return Math.max.apply(null, state.news.map(item => item.key));
        },
        isAuthenticated: (state) => {
            return !!state.user;
        },
        userInfo: (state) => {
            return state.user;
        }
    },
    actions: {
        createNews ({commit}, news) {
            commit('createNews', news);
        },
        deleteNews ({commit}, item) {
            commit('deleteNews', item);
        },
        setSpinning ({commit}, value) {
            commit('setSpinning', value);
        },
        setLanguage ({commit}, lang) {
            commit('setLanguage', lang);
        },
        async saveUser ({commit}, user) {
            commit('saveUser', user.username);
        },
        async logOut ({commit}) {
            let user = null;
            commit('logOut', user);
        }
    }
});

export default store;