<template>
    <div class="sidebar">
        <h4>{{$t('sidebar.title')}}</h4>
        <ul>
            <li v-for="item in data" :key="item.index">
                <router-link :to="item.link" exact>
                    <a-icon :type="item.icon" />
                    <span>{{$t(item.name)}}</span>
                </router-link>
            </li>
        </ul>
        <a-button @click="logOut">Logout</a-button>
    </div>
</template>

<script>
    import data from "../../constant";
    import links from "../../router/links";

    export default {
        name: "Sidebar",
        data() {
            return {
                data: data.menu,
            }
        },
        methods: {
            async logOut() {
                await this.$store.dispatch('logOut');
                this.$router.push(links.LOGIN);
            }
        }
    }
</script>

<style scoped lang="scss">
    .sidebar {
        padding: 10px 24px;
        h4 {
            color: #fff;
            font-weight: 400;
            text-transform: uppercase;
            font-size: 0.9rem;
        }
        ul {
            margin: 0 0 0 10px;
            padding: 0;
            list-style: none;
            li {
                padding: 4px 0;
                a {
                    color: #fff;
                    font-size: 0.82rem;
                    display: flex;
                    align-items: center;
                    & > span {
                        display: block;
                        padding: 4px 8px;
                        transition: 0.2s;
                        border-radius: 2px;
                        width: 100%;
                        margin-left: 10px;
                    }
                    &.router-link-exact-active {
                        opacity: 1;
                        & > span {
                            background: rgba(255,255,255,0.2)!important;
                        }
                    }
                    &:hover {
                        opacity: 1;
                        & > span {
                            background: rgba(255,255,255,0.1);
                        }
                    }
                }
            }
        }
    }
</style>